import React from 'react';

export default function DashboardPage() {
  return (
    <div className="dashboard-page">
      <h1 className="text-2xl font-bold mb-6">Welcome to Dashboard</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-lg font-semibold mb-2">Users</h2>
          <p className="text-gray-600">Manage system users</p>
        </div>
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-lg font-semibold mb-2">Visa Applications</h2>
          <p className="text-gray-600">Process visa applications</p>
        </div>
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-lg font-semibold mb-2">MOU Management</h2>
          <p className="text-gray-600">Manage memorandums of understanding</p>
        </div>
      </div>
    </div>
  );
}
